// =============================================================================
// VERCEL CRON ENDPOINT: Facilitator Health Probes
// =============================================================================
// GET /api/cron/facilitator-probes
// Runs facilitator health probes based on configured intervals
// Called by Vercel Cron every minute (schedule: "* * * * *")
// The job itself enforces 30s/2m intervals per probe config
//
// Note: Currently HARD DISABLED - see HARD_DISABLE_PROBES flag below

import { NextRequest } from 'next/server';
import { runFacilitatorHealthProbes } from '@/integrations/x402/facilitators/run-facilitator-health-probes';
import { getProbeStateRepo } from '@/integrations/x402/facilitators/facilitator-probe-state-repo';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';

const logger = createLogger({ component: 'FacilitatorProbesCron' });

// =============================================================================
// HARD DISABLE - Probes are completely OFF regardless of environment variables
// To re-enable: change HARD_DISABLE_PROBES to false in BOTH this file AND 
// facilitator-probe-config.ts, then set FACILITATOR_PROBES_ENABLED=true env var
// =============================================================================
const HARD_DISABLE_PROBES = true; // <-- SET TO false TO RE-ENABLE

// =============================================================================
// CRON HANDLER
// =============================================================================

async function facilitatorProbesHandler(
  _request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;

  // HARD DISABLE - always skip when this flag is true
  if (HARD_DISABLE_PROBES) {
    logger.info({ runId, requestId, msg: 'Skipping facilitator probes - HARD DISABLED in code' });
    return {
      ok: true,
      skipped: true,
      reason: 'hard-disabled',
      message: 'Facilitator probes are HARD DISABLED in code. Edit route.ts to re-enable.',
    };
  }

  // Environment gating - only run in production
  if (process.env.NODE_ENV !== 'production') {
    logger.info({ runId, requestId, msg: 'Skipping facilitator probes - not in production' });
    return {
      ok: true,
      skipped: true,
      reason: 'non-production',
      environment: process.env.NODE_ENV,
    };
  }

  logger.info({ runId, requestId, msg: 'Starting facilitator health probe job' });

  const probeStateRepo = getProbeStateRepo();
  const result = await runFacilitatorHealthProbes({
    probeStateRepo,
  });

  logger.info({
    runId,
    requestId,
    executed: result.executed,
    skipped: result.skipped,
    errors: result.errors,
    errorDetails: result.errorDetails,
    msg: `Facilitator health probes completed: executed=${result.executed}, skipped=${result.skipped}, errors=${result.errors}`,
  });
  
  // Log error details to console for visibility in Vercel logs
  if (result.errors > 0 && result.errorDetails) {
    console.error('[PROBE JOB ERRORS]', JSON.stringify(result.errorDetails, null, 2));
  }

  return {
    ok: result.errors === 0,
    executed: result.executed,
    skipped: result.skipped,
    errors: result.errors,
    errorDetails: result.errorDetails,
    timestamp: new Date().toISOString(),
  };
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 60;

export const GET = withCronJobTracking(facilitatorProbesHandler, {
  jobId: 'facilitator-probes',
  enabledEnvVar: 'FACILITATOR_PROBES_ENABLED',
  timeout: 55000, // 55s (leave buffer for wrapper overhead)
  minInterval: 50 * 1000, // 50 seconds (schedule is every minute)
});
