// =============================================================================
// FACILITATOR PROBE STATE REPOSITORY
// =============================================================================
// Manages last run time state for probe configurations
// Used by Vercel Cron to determine which probes are due to run

import { getDb } from '@/db/client';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'FacilitatorProbeStateRepo' });

export type FacilitatorProbeState = {
  probeConfigId: string;
  lastRunAt: Date | null;
};

export interface FacilitatorProbeStateRepo {
  getState(probeConfigId: string): Promise<FacilitatorProbeState | null>;
  upsertState(probeConfigId: string, lastRunAt: Date): Promise<void>;
}

/**
 * PostgreSQL implementation of FacilitatorProbeStateRepo
 */
export class PostgresFacilitatorProbeStateRepo implements FacilitatorProbeStateRepo {
  async getState(probeConfigId: string): Promise<FacilitatorProbeState | null> {
    const db = getDb();
    const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';

    if (!isPostgres) {
      logger.warn('PostgreSQL required for probe state - returning null');
      return null;
    }

    try {
      const result = await (db as any).pool.query(
        `SELECT probe_config_id, last_run_at 
         FROM facilitator_probe_state 
         WHERE probe_config_id = $1 
         LIMIT 1`,
        [probeConfigId]
      );

      if (result.rows.length === 0) {
        return null;
      }

      return {
        probeConfigId: result.rows[0].probe_config_id,
        lastRunAt: result.rows[0].last_run_at ? new Date(result.rows[0].last_run_at) : null,
      };
    } catch (error) {
      logger.error({ error, probeConfigId }, 'Failed to get probe state');
      throw error;
    }
  }

  async upsertState(probeConfigId: string, lastRunAt: Date): Promise<void> {
    const db = getDb();
    const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';

    if (!isPostgres) {
      logger.warn('PostgreSQL required for probe state - skipping upsert');
      return;
    }

    try {
      const now = new Date().toISOString();
      await (db as any).pool.query(
        `INSERT INTO facilitator_probe_state (probe_config_id, last_run_at, updated_at)
         VALUES ($1, $2, $3)
         ON CONFLICT (probe_config_id) 
         DO UPDATE SET last_run_at = $2, updated_at = $3`,
        [probeConfigId, lastRunAt.toISOString(), now]
      );
    } catch (error) {
      logger.error({ error, probeConfigId, lastRunAt }, 'Failed to upsert probe state');
      throw error;
    }
  }
}

// Singleton instance
let probeStateRepoInstance: FacilitatorProbeStateRepo | null = null;

/**
 * Get the probe state repository instance
 */
export function getProbeStateRepo(): FacilitatorProbeStateRepo {
  if (!probeStateRepoInstance) {
    probeStateRepoInstance = new PostgresFacilitatorProbeStateRepo();
  }
  return probeStateRepoInstance;
}





