// =============================================================================
// FACILITATOR PROBE CONFIGURATION
// =============================================================================
// Configuration for facilitator health probes
// Defines which facilitators/networks/tokens to probe and at what intervals

export type FacilitatorProbeConfig = {
  id: string; // e.g. "cdp-base-usdc"
  facilitatorId: string; // "cdp"
  network: string; // e.g. "eip155:8453"
  token: string; // e.g. "USDC"
  desiredIntervalSeconds: number; // e.g. 30 or 120
  enabled: boolean;
};

/**
 * Default probe configurations
 * Intervals can be configured via environment variables:
 * - CDP_PROBE_INTERVAL_SECONDS (default: 30)
 * - PAYAI_PROBE_INTERVAL_SECONDS (default: 120)
 * - X402RS_PROBE_INTERVAL_SECONDS (default: 60)
 * - DEXTER_PROBE_INTERVAL_SECONDS (default: 60)
 * - CRONOS_PROBE_INTERVAL_SECONDS (default: 60)
 * 
 * MASTER SWITCH: Set FACILITATOR_PROBES_ENABLED=true to enable all probes
 * Currently DISABLED pending x402scan analytics integration
 * 
 * TODO: Add per-facilitator rate limit configuration
 * - CDP may have different rate limits than PayAI
 * - Cronos has 5 req/min rate limit for verify/settle
 * - Consider adding FACILITATOR_RATE_LIMIT_CONFIG env var for custom limits
 */

// =============================================================================
// HARD DISABLE - Probes are OFF regardless of environment variables
// To re-enable: change HARD_DISABLE_PROBES to false AND set env var
// =============================================================================
const HARD_DISABLE_PROBES = true; // <-- SET TO false TO RE-ENABLE

// Master switch for all probes - requires BOTH hard disable off AND env var set
const PROBES_MASTER_ENABLED = !HARD_DISABLE_PROBES && process.env.FACILITATOR_PROBES_ENABLED === 'true';

export const FACILITATOR_PROBE_CONFIGS: FacilitatorProbeConfig[] = [
  {
    id: 'cdp-base-usdc',
    facilitatorId: 'cdp',
    network: 'eip155:8453', // Base mainnet
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.CDP_PROBE_INTERVAL_SECONDS || '30', 10), // Configurable via env
    enabled: PROBES_MASTER_ENABLED, // DISABLED - pending x402scan integration
  },
  {
    id: 'payai-base-usdc',
    facilitatorId: 'payai',
    network: 'eip155:8453', // Base mainnet
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.PAYAI_PROBE_INTERVAL_SECONDS || '120', 10), // Configurable via env
    enabled: PROBES_MASTER_ENABLED, // DISABLED - pending x402scan integration
  },
  // ==========================================================================
  // X402rs Facilitator Probes
  // Public hosted endpoint: https://facilitator.x402.rs
  // Rust-based community facilitator with Base and XDC support
  // ==========================================================================
  {
    id: 'x402rs-base-usdc',
    facilitatorId: 'x402rs',
    network: 'eip155:8453', // Base mainnet
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.X402RS_PROBE_INTERVAL_SECONDS || '60', 10),
    enabled: PROBES_MASTER_ENABLED && process.env.X402RS_ENABLED !== 'false', // DISABLED - pending x402scan integration
  },
  // Base Sepolia disabled - not needed
  // {
  //   id: 'x402rs-base-sepolia-usdc',
  //   facilitatorId: 'x402rs',
  //   network: 'eip155:84532', // Base Sepolia (testnet)
  //   token: 'USDC',
  //   desiredIntervalSeconds: parseInt(process.env.X402RS_PROBE_INTERVAL_SECONDS || '60', 10),
  //   enabled: PROBES_MASTER_ENABLED && process.env.X402RS_ENABLED !== 'false',
  // },
  // XDC probe (optional - enable via env when needed)
  {
    id: 'x402rs-xdc-usdc',
    facilitatorId: 'x402rs',
    network: 'xdc:50', // XDC mainnet
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.X402RS_PROBE_INTERVAL_SECONDS || '120', 10), // Every 2 minutes for XDC
    enabled: PROBES_MASTER_ENABLED && process.env.X402RS_XDC_PROBE_ENABLED === 'true', // Disabled by default
  },
  // Temporarily disabled - CDP does not support solana/USDC/x402
  // Re-enable when a supported Solana network/asset combo is confirmed
  // {
  //   id: 'cdp-solana-usdc',
  //   facilitatorId: 'cdp',
  //   network: 'solana:mainnet',
  //   token: 'USDC',
  //   desiredIntervalSeconds: 120, // Slower cadence, e.g. 2 minutes
  //   enabled: false,
  // },
  // ==========================================================================
  // Dexter Facilitator Probes
  // Public hosted endpoint: https://facilitator.dexter.cash
  // Solana-native x402 facilitator with Solana mainnet/devnet support
  // ==========================================================================
  {
    id: 'dexter-solana-usdc',
    facilitatorId: 'dexter',
    network: 'solana:101', // Solana mainnet (CAIP-2)
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.DEXTER_PROBE_INTERVAL_SECONDS || '60', 10),
    enabled: PROBES_MASTER_ENABLED && process.env.DEXTER_ENABLED !== 'false', // DISABLED - pending x402scan integration
  },
  // Solana devnet probe (optional - enable via env when needed for testing)
  {
    id: 'dexter-solana-devnet-usdc',
    facilitatorId: 'dexter',
    network: 'solana:102', // Solana devnet (CAIP-2)
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.DEXTER_PROBE_INTERVAL_SECONDS || '120', 10), // Every 2 minutes for devnet
    enabled: PROBES_MASTER_ENABLED && process.env.DEXTER_DEVNET_PROBE_ENABLED === 'true', // Disabled by default
  },
  // ==========================================================================
  // Cronos x402 Facilitator Probes
  // Base URL: https://facilitator.cronoslabs.org
  // Official Cronos Labs x402 facilitator with Cronos mainnet/testnet support
  // Documentation: https://docs.cronos.org/cronos-x402-facilitator
  // Rate limits: 5 requests per minute per IP for settle/verify
  // ==========================================================================
  {
    id: 'cronos-mainnet-usdc',
    facilitatorId: 'cronos',
    network: 'eip155:25', // Cronos mainnet (CAIP-2, chain ID 25)
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.CRONOS_PROBE_INTERVAL_SECONDS || '60', 10),
    enabled: PROBES_MASTER_ENABLED && process.env.CRONOS_ENABLED !== 'false', // DISABLED - pending x402scan integration
  },
  // Cronos testnet probe (optional - enable via env when needed for testing)
  {
    id: 'cronos-testnet-usdc',
    facilitatorId: 'cronos',
    network: 'eip155:338', // Cronos testnet (CAIP-2, chain ID 338)
    token: 'USDC',
    desiredIntervalSeconds: parseInt(process.env.CRONOS_PROBE_INTERVAL_SECONDS || '120', 10), // Every 2 minutes for testnet
    enabled: PROBES_MASTER_ENABLED && process.env.CRONOS_TESTNET_PROBE_ENABLED === 'true', // Disabled by default
  },
];

/**
 * Get enabled probe configurations
 */
export function getEnabledProbeConfigs(): FacilitatorProbeConfig[] {
  return FACILITATOR_PROBE_CONFIGS.filter((c) => c.enabled);
}

/**
 * Get probe config by ID
 */
export function getProbeConfigById(id: string): FacilitatorProbeConfig | undefined {
  return FACILITATOR_PROBE_CONFIGS.find((c) => c.id === id);
}

