// =============================================================================
// RUN FACILITATOR HEALTH PROBES (Cron Job)
// =============================================================================
// Core job function that runs facilitator health probes via Vercel Cron
// Checks last run time vs desired intervals and executes probes that are due

import { getEnabledProbeConfigs, type FacilitatorProbeConfig } from './facilitator-probe-config';
import type { FacilitatorProbeStateRepo } from './facilitator-probe-state-repo';
import { createRouteAttempt, recordFacilitatorProbeEvent, type RecordFacilitatorProbeEventArgs } from '@/db/smf';
import { getOrCreateProbeRoute } from './facilitator-probes';
import { runSingleFacilitatorProbe } from './run-single-facilitator-probe';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'RunFacilitatorHealthProbes' });

// Dev-time guard to verify function is imported correctly
if (process.env.NODE_ENV !== 'production') {
  console.log('recordFacilitatorProbeEvent typeof:', typeof recordFacilitatorProbeEvent);
}

export interface RunFacilitatorHealthProbesDeps {
  probeStateRepo: FacilitatorProbeStateRepo;
  now?: () => Date;
}

/**
 * Run facilitator health probes for all enabled configurations
 * Only executes probes that are due based on their desired intervals
 */
export async function runFacilitatorHealthProbes(
  deps: RunFacilitatorHealthProbesDeps
): Promise<{ executed: number; skipped: number; errors: number; errorDetails?: Array<{ probeConfigId: string; error: string }> }> {
  const now = deps.now?.() ?? new Date();
  const configs = getEnabledProbeConfigs();

  logger.info(
    { 
      probeCount: configs.length,
      probeConfigs: configs.map(c => ({ id: c.id, facilitatorId: c.facilitatorId, enabled: c.enabled }))
    }, 
    'Starting facilitator health probe job'
  );

  let executed = 0;
  let skipped = 0;
  let errors = 0;
  const errorDetails: Array<{ probeConfigId: string; error: string }> = [];

  for (const config of configs) {
    try {
      // Check if probe is due to run
      const state = await deps.probeStateRepo.getState(config.id);
      const lastRunAt = state?.lastRunAt ?? null;

      const secondsSinceLastRun = lastRunAt
        ? (now.getTime() - lastRunAt.getTime()) / 1000
        : Number.POSITIVE_INFINITY;

      if (secondsSinceLastRun < config.desiredIntervalSeconds) {
        const remainingSeconds = Math.ceil(config.desiredIntervalSeconds - secondsSinceLastRun);
        logger.debug(
          {
            probeConfigId: config.id,
            facilitatorId: config.facilitatorId,
            secondsSinceLastRun: Math.floor(secondsSinceLastRun),
            remainingSeconds,
            lastRunAt: lastRunAt?.toISOString(),
          },
          'Probe not due yet, skipping'
        );
        skipped++;
        continue;
      }

      // Log that we're about to execute (helps debug if it fails silently)
      logger.info(
        {
          probeConfigId: config.id,
          facilitatorId: config.facilitatorId,
          network: config.network,
          token: config.token,
          secondsSinceLastRun: Math.floor(secondsSinceLastRun),
        },
        'Probe is due, will execute'
      );

      // Execute probe
      logger.info(
        {
          probeConfigId: config.id,
          facilitatorId: config.facilitatorId,
          network: config.network,
          token: config.token,
          secondsSinceLastRun: Math.floor(secondsSinceLastRun),
        },
        'Executing probe'
      );

      const startedAt = new Date();
      let probeResult;
      try {
        probeResult = await runSingleFacilitatorProbe(config);
      } catch (probeError) {
        throw new Error(`runSingleFacilitatorProbe failed: ${probeError instanceof Error ? probeError.message : String(probeError)}`, { cause: probeError });
      }
      const completedAt = new Date();
      const latencyMs = completedAt.getTime() - startedAt.getTime();

      // Map probe result to route attempt result type
      let result: 'success' | 'failure' | 'timeout' | 'rate_limited' | 'invalid_request' | 'network_error' =
        'failure';
      let errorCode: string | null = null;
      let rawStatus: number | null = null;

      if (probeResult.ok) {
        result = 'success';
      } else {
        // Map error codes to result types
        switch (probeResult.errorCode) {
          case 'timeout':
            result = 'timeout';
            break;
          case 'rate_limited':
            result = 'rate_limited';
            rawStatus = 429;
            break;
          case 'network_error':
            result = 'network_error';
            break;
          case 'invalid_request':
            result = 'invalid_request';
            break;
          default:
            result = 'failure';
        }
        errorCode = probeResult.errorCode || null;
        
        // Use facilitatorStatus as rawStatus if available
        if (probeResult.facilitatorStatus !== undefined) {
          rawStatus = probeResult.facilitatorStatus;
        }
      }

      // Get or create route entry for this probe
      let probeRouteId;
      try {
        probeRouteId = await getOrCreateProbeRoute(
          config.facilitatorId,
          config.network,
          config.token
        );
      } catch (routeError) {
        throw new Error(`getOrCreateProbeRoute failed: ${routeError instanceof Error ? routeError.message : String(routeError)}`, { cause: routeError });
      }

      // Log probe attempt to database
      try {
        await createRouteAttempt({
        route_id: probeRouteId,
        facilitator_id: config.facilitatorId,
        phase: 'verify',
        result,
        latency_ms: probeResult.latencyMs,
        error_code: errorCode,
        raw_status: rawStatus,
        is_probe: true,
      });
      } catch (attemptError) {
        throw new Error(`createRouteAttempt failed: ${attemptError instanceof Error ? attemptError.message : String(attemptError)}`, { cause: attemptError });
      }

      // Record probe event for analytics (lightweight summary data)
      // Enhanced logging added to capture full exception details
      try {
        await recordFacilitatorProbeEvent({
          probe_config_id: config.id,
          facilitator_id: config.facilitatorId,
          network: config.network,
          token: config.token,
          result: result === 'success' ? 'success' : 'failure',
          error_code: errorCode || null,
          facilitator_status: probeResult.facilitatorStatus || null,
          facilitator_error_code: probeResult.facilitatorErrorCode || null,
          latency_ms: probeResult.latencyMs,
        });
      } catch (eventError) {
        // Log full exception details to diagnose DB constraint/validation issues
        const eventErrorMessage = eventError instanceof Error ? eventError.message : String(eventError);
        const eventErrorStack = eventError instanceof Error ? eventError.stack : undefined;
        const eventErrorName = eventError instanceof Error ? eventError.name : 'UnknownError';
        const eventErrorCode = (eventError as any)?.code;
        const eventErrorCause = (eventError as any)?.cause;
        
        // Extract PostgreSQL error details if available
        const pgError = (eventError as any)?.code ? {
          code: (eventError as any).code,
          detail: (eventError as any).detail,
          hint: (eventError as any).hint,
          constraint: (eventError as any).constraint,
          table: (eventError as any).table,
          column: (eventError as any).column,
        } : null;
        
        logger.error(
          {
            error: eventError,
            errorMessage: eventErrorMessage,
            errorStack: eventErrorStack,
            errorName: eventErrorName,
            errorCode: eventErrorCode,
            errorCause: eventErrorCause,
            pgError,
            probeConfigId: config.id,
            facilitatorId: config.facilitatorId,
            network: config.network,
            token: config.token,
          },
          'Failed to record probe event - full exception details'
        );
        
        // Also log to console.error for Vercel visibility
        console.error(`[PROBE EVENT ERROR] ${config.id}:`, {
          message: eventErrorMessage,
          name: eventErrorName,
          code: eventErrorCode,
          stack: eventErrorStack,
          cause: eventErrorCause,
          pgError,
        });
      }

      // Update last run time (even if probe failed, we still update state to allow retry)
      try {
        await deps.probeStateRepo.upsertState(config.id, now);
      } catch (stateError) {
        // Log but don't fail the probe if state update fails
        logger.warn({ error: stateError, probeConfigId: config.id }, 'Failed to update probe state');
      }

      logger.info(
        {
          component: "RunFacilitatorHealthProbes",
          probeConfigId: config.id,
          facilitatorId: config.facilitatorId,
          network: config.network,
          token: config.token,
          result,
          latencyMs: probeResult.latencyMs,
          errorCode,
          facilitatorStatus: probeResult.facilitatorStatus,
          facilitatorErrorCode: probeResult.facilitatorErrorCode,
          facilitatorError: probeResult.errorMessage,
          facilitatorRaw: probeResult.facilitatorStatus ? { status: probeResult.facilitatorStatus, errorCode: probeResult.facilitatorErrorCode } : undefined,
        },
        'Probe completed'
      );

      executed++;
    } catch (error) {
      errors++;
      const errorMessage = error instanceof Error ? error.message : String(error);
      const errorStack = error instanceof Error ? error.stack : undefined;
      const errorName = error instanceof Error ? error.name : 'UnknownError';
      
      // Try to extract more details from the error
      let fullErrorDetails = errorMessage;
      if (error && typeof error === 'object') {
        try {
          const errorObj = error as any;
          if (errorObj.cause) {
            fullErrorDetails += ` | Cause: ${errorObj.cause instanceof Error ? errorObj.cause.message : String(errorObj.cause)}`;
          }
          if (errorObj.code) {
            fullErrorDetails += ` | Code: ${errorObj.code}`;
          }
          if (errorObj.statusCode) {
            fullErrorDetails += ` | Status: ${errorObj.statusCode}`;
          }
        } catch {
          // Ignore errors in error extraction
        }
      }
      
      errorDetails.push({
        probeConfigId: config.id,
        error: fullErrorDetails,
      });
      
      // Log error with full details - this will show in Vercel logs
      logger.error(
        {
          error,
          errorMessage,
          errorStack,
          errorName,
          fullErrorDetails,
          probeConfigId: config.id,
          facilitatorId: config.facilitatorId,
          network: config.network,
          token: config.token,
        },
        `âŒ PROBE FAILED: ${config.id} - ${fullErrorDetails}`
      );
      
      // Also log to console.error for visibility in Vercel logs with full details
      console.error(`[PROBE ERROR] ${config.id}:`, fullErrorDetails);
      if (errorStack) {
        console.error(`[PROBE ERROR STACK] ${config.id}:`, errorStack);
      }
      // Log the full error object for debugging (safely serialize)
      try {
        const errorObj = error instanceof Error 
          ? { name: error.name, message: error.message, stack: error.stack, ...(error as any) }
          : error;
        console.error(`[PROBE ERROR OBJECT] ${config.id}:`, JSON.stringify(errorObj, null, 2));
      } catch (serializeError) {
        console.error(`[PROBE ERROR OBJECT] ${config.id}:`, String(error));
      }
      
      // Update probe state even on failure to allow retry after interval
      // This prevents probes from getting stuck if they fail
      try {
        await deps.probeStateRepo.upsertState(config.id, now);
        logger.debug({ probeConfigId: config.id }, 'Updated probe state after failure to allow retry');
      } catch (stateError) {
        logger.warn({ error: stateError, probeConfigId: config.id }, 'Failed to update probe state after failure');
      }
      
      // Continue with other probes even if one fails
    }
  }

  logger.info(
    { executed, skipped, errors, total: configs.length },
    'Facilitator health probe job completed'
  );

  return { executed, skipped, errors, errorDetails: errorDetails.length > 0 ? errorDetails : undefined };
}

